	module.exports = {"data":{"page":{"html":"<h2>Introduction</h2>\n<p>Lighthouse allows you to create a <a href=\"http://facebook.github.io/graphql/\">GraphQL</a> Server with <a href=\"https://laravel.com/docs\">Laravel</a>. This package extends <a href=\"https://github.com/webonyx/graphql-php\">graphql-php</a> and works with <a href=\"https://facebook.github.io/relay/\">Relay Modern</a>, <a href=\"https://www.apollographql.com/client\">Apollo Client</a>, <a href=\"https://github.com/FormidableLabs/urql\">urql</a> and any other client that works with GraphQL backends (or that are capable of making POST requests).</p>\n<p>Lighthouse v2.0 takes a step closer to \"schema first\" development by allowing you to create <code class=\"language-text\">.graphql</code> schema file(s) to describe your data while leveraging a directives to enhance/customize the schema to fit your data requirements.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-graphql\"><code class=\"language-graphql\">type User <span class=\"token punctuation\">{</span>\n  <span class=\"token attr-name\">id</span><span class=\"token punctuation\">:</span> ID<span class=\"token operator\">!</span>\n  <span class=\"token attr-name\">name</span><span class=\"token punctuation\">:</span> String<span class=\"token operator\">!</span>\n  <span class=\"token attr-name\">email</span><span class=\"token punctuation\">:</span> String<span class=\"token operator\">!</span>\n  <span class=\"token attr-name\">posts</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>Post<span class=\"token operator\">!</span><span class=\"token punctuation\">]</span><span class=\"token operator\">!</span> <span class=\"token directive function\">@hasMany</span>\n<span class=\"token punctuation\">}</span>\n\ntype Post <span class=\"token punctuation\">{</span>\n  <span class=\"token attr-name\">id</span><span class=\"token punctuation\">:</span> ID<span class=\"token operator\">!</span>\n  <span class=\"token attr-name\">title</span><span class=\"token punctuation\">:</span> String<span class=\"token operator\">!</span>\n  <span class=\"token attr-name\">content</span><span class=\"token punctuation\">:</span> String<span class=\"token operator\">!</span>\n  <span class=\"token attr-name\">created_at</span><span class=\"token punctuation\">:</span> String<span class=\"token operator\">!</span>\n  <span class=\"token attr-name\">updated_at</span><span class=\"token punctuation\">:</span> String\n  <span class=\"token attr-name\">author</span><span class=\"token punctuation\">:</span> User <span class=\"token directive function\">@belongsTo</span>\n<span class=\"token punctuation\">}</span>\n\ntype Query <span class=\"token punctuation\">{</span>\n  <span class=\"token attr-name\">me</span><span class=\"token punctuation\">:</span> User <span class=\"token directive function\">@auth</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n      </div>\n<p>With just this schema file (along w/ Eloquent models and migrations set up), you have a fully functioning GraphQL server with no additional code! The docs will walk you through what directives are available, how you can create your own and how to create your own resolvers, so let's get started!</p>","details":{"title":"Lighthouse: Introduction","date":"July 12, 2017","path":"/introduction"}},"pages":{"edges":[{"post":{"id":"/Users/christophermoore/Sites/nuwave/lighthouse-gatsby/src/pages/v2.0/configuration.md absPath of file >>> MarkdownRemark","details":{"title":"Configuration","path":"/docs/2.0/config"}}},{"post":{"id":"/Users/christophermoore/Sites/nuwave/lighthouse-gatsby/src/pages/v2.0/directives.md absPath of file >>> MarkdownRemark","details":{"title":"Directives","path":"/docs/2.0/directives"}}},{"post":{"id":"/Users/christophermoore/Sites/nuwave/lighthouse-gatsby/src/pages/v2.0/installation.md absPath of file >>> MarkdownRemark","details":{"title":"Installation","path":"/docs/2.0/installation"}}},{"post":{"id":"/Users/christophermoore/Sites/nuwave/lighthouse-gatsby/src/pages/v2.0/schema.md absPath of file >>> MarkdownRemark","details":{"title":"Schema","path":"/docs/2.0/schema"}}},{"post":{"id":"/Users/christophermoore/Sites/nuwave/lighthouse-gatsby/src/pages/v2.0/walkthrough.md absPath of file >>> MarkdownRemark","details":{"title":"Walkthrough","path":"/docs/2.0/walkthrough"}}}]}},"pathContext":{}}

